.header {
  flex-basis: 33%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  width: 33vw;
  height: 100vh;
}
.header__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.header__title {
  font-size: 1.5em;
  font-family: sans-serif;
}
.header__subtitle {
  font-size: 1em;
  font-family: sans-serif;
}

.skillImg {
  width: 50px;
  height: 50px;
}

.skills {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

@media screen and (max-width: 763.98px) {
  .header {
    width: 100vw;
    height: 33vh;
  }
  .skillImg {
    width: 35px;
    height: 35px;
  }
}/*# sourceMappingURL=header.css.map */